import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Coins, Plus, Minus } from "lucide-react";
import { useAdminModifyPoints } from "@/hooks/usePoints";
import { toast } from "sonner";

const formSchema = z.object({
  amount: z.number().min(1, "积分数量必须大于0"),
  reason: z.string().min(1, "请填写原因"),
});

type FormData = z.infer<typeof formSchema>;

interface GrantPointsDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  user: any;
  type: "grant" | "deduct";
}

export const GrantPointsDialog = ({ open, onOpenChange, user, type }: GrantPointsDialogProps) => {
  const { t } = useTranslation();
  const modifyPoints = useAdminModifyPoints();

  const form = useForm<FormData>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      amount: 10,
      reason: "",
    },
  });

  const onSubmit = async (data: FormData) => {
    if (!user) return;

    const amount = type === "deduct" ? -data.amount : data.amount;

    try {
      const result = await modifyPoints.mutateAsync({
        userId: user.id,
        amount,
        reason: data.reason,
      });

      if (result.success) {
        toast.success(
          type === "grant" 
            ? t("admin.points.grantSuccess") 
            : t("admin.points.deductSuccess")
        );
        form.reset();
        onOpenChange(false);
      } else {
        toast.error(result.error || t("common.error"));
      }
    } catch (error) {
      console.error("Error modifying points:", error);
      toast.error(t("common.error"));
    }
  };

  if (!user) return null;

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            {type === "grant" ? (
              <>
                <Plus className="h-5 w-5 text-green-500" />
                {t("admin.points.grantPoints")}
              </>
            ) : (
              <>
                <Minus className="h-5 w-5 text-red-500" />
                {t("admin.points.deductPoints")}
              </>
            )}
          </DialogTitle>
          <DialogDescription>
            {t("admin.points.targetUser")}: <strong>{user.display_name}</strong>
            <br />
            {t("admin.points.currentPoints")}: <strong>{user.points}</strong>
          </DialogDescription>
        </DialogHeader>

        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
            <FormField
              control={form.control}
              name="amount"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>{t("admin.points.amount")}</FormLabel>
                  <FormControl>
                    <div className="relative">
                      <Coins className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                      <Input 
                        type="number" 
                        className="pl-10"
                        min={1}
                        {...field}
                        onChange={e => field.onChange(parseInt(e.target.value) || 0)}
                      />
                    </div>
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />

            <FormField
              control={form.control}
              name="reason"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>{t("admin.points.reason")} *</FormLabel>
                  <FormControl>
                    <Textarea 
                      {...field} 
                      placeholder={
                        type === "grant" 
                          ? t("admin.points.grantReasonPlaceholder")
                          : t("admin.points.deductReasonPlaceholder")
                      }
                      rows={3}
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />

            <div className="flex justify-end gap-2">
              <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>
                {t("common.cancel")}
              </Button>
              <Button 
                type="submit" 
                disabled={modifyPoints.isPending}
                variant={type === "deduct" ? "destructive" : "default"}
              >
                {modifyPoints.isPending 
                  ? t("common.processing") 
                  : type === "grant" 
                    ? t("admin.points.confirmGrant")
                    : t("admin.points.confirmDeduct")
                }
              </Button>
            </div>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
};
