import { useState } from "react";
import { useTranslation } from "react-i18next";
import { Coins, Search, Plus, Minus, History, Users } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { useAllPointsTransactions } from "@/hooks/usePoints";
import { supabase } from "@/integrations/supabase/client";
import { GrantPointsDialog } from "@/components/admin/GrantPointsDialog";
import { format } from "date-fns";
import { toast } from "sonner";

const AdminPoints = () => {
  const { t } = useTranslation();
  const { data: transactions, isLoading } = useAllPointsTransactions();
  
  const [searchQuery, setSearchQuery] = useState("");
  const [searchResults, setSearchResults] = useState<any[]>([]);
  const [isSearching, setIsSearching] = useState(false);
  const [grantDialogOpen, setGrantDialogOpen] = useState(false);
  const [selectedUser, setSelectedUser] = useState<any>(null);
  const [grantType, setGrantType] = useState<"grant" | "deduct">("grant");

  const handleSearch = async () => {
    if (!searchQuery.trim()) {
      toast.error(t("admin.points.enterSearchTerm"));
      return;
    }

    setIsSearching(true);
    try {
      const { data, error } = await supabase
        .from("profiles")
        .select("id, display_name, avatar_url, points")
        .or(`display_name.ilike.%${searchQuery}%,id.eq.${searchQuery.match(/^[0-9a-f-]{36}$/i) ? searchQuery : '00000000-0000-0000-0000-000000000000'}`)
        .limit(20);

      if (error) throw error;
      setSearchResults(data || []);
    } catch (error) {
      console.error("Search error:", error);
      toast.error(t("common.error"));
    } finally {
      setIsSearching(false);
    }
  };

  const handleGrantPoints = (user: any) => {
    setSelectedUser(user);
    setGrantType("grant");
    setGrantDialogOpen(true);
  };

  const handleDeductPoints = (user: any) => {
    setSelectedUser(user);
    setGrantType("deduct");
    setGrantDialogOpen(true);
  };

  const getTransactionTypeLabel = (type: string) => {
    switch (type) {
      case "comment": return t("admin.points.typeComment");
      case "admin_grant": return t("admin.points.typeAdminGrant");
      case "admin_deduct": return t("admin.points.typeAdminDeduct");
      case "purchase": return t("admin.points.typePurchase");
      case "refund": return t("admin.points.typeRefund");
      case "expire": return t("admin.points.typeExpire");
      default: return type;
    }
  };

  const getTransactionTypeBadge = (type: string, amount: number) => {
    const isPositive = amount > 0;
    return (
      <Badge variant={isPositive ? "default" : "secondary"}>
        {getTransactionTypeLabel(type)}
      </Badge>
    );
  };

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-2xl font-bold">{t("admin.points.title")}</h1>
        <p className="text-muted-foreground">{t("admin.points.description")}</p>
      </div>

      <Tabs defaultValue="users" className="space-y-4">
        <TabsList>
          <TabsTrigger value="users" className="flex items-center gap-2">
            <Users className="h-4 w-4" />
            {t("admin.points.userManagement")}
          </TabsTrigger>
          <TabsTrigger value="history" className="flex items-center gap-2">
            <History className="h-4 w-4" />
            {t("admin.points.transactionHistory")}
          </TabsTrigger>
        </TabsList>

        <TabsContent value="users" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Search className="h-5 w-5" />
                {t("admin.points.searchUser")}
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex gap-2">
                <Input
                  placeholder={t("admin.points.searchPlaceholder")}
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  onKeyDown={(e) => e.key === "Enter" && handleSearch()}
                />
                <Button onClick={handleSearch} disabled={isSearching}>
                  <Search className="h-4 w-4 mr-2" />
                  {t("common.search")}
                </Button>
              </div>

              {searchResults.length > 0 && (
                <div className="mt-4">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead>{t("admin.points.user")}</TableHead>
                        <TableHead>{t("admin.points.currentPoints")}</TableHead>
                        <TableHead className="text-right">{t("common.actions")}</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {searchResults.map((user) => (
                        <TableRow key={user.id}>
                          <TableCell>
                            <div className="flex items-center gap-3">
                              {user.avatar_url ? (
                                <img 
                                  src={user.avatar_url} 
                                  alt={user.display_name} 
                                  className="w-8 h-8 rounded-full object-cover"
                                />
                              ) : (
                                <div className="w-8 h-8 rounded-full bg-muted flex items-center justify-center">
                                  {user.display_name?.charAt(0).toUpperCase()}
                                </div>
                              )}
                              <div>
                                <div className="font-medium">{user.display_name}</div>
                                <div className="text-xs text-muted-foreground">{user.id}</div>
                              </div>
                            </div>
                          </TableCell>
                          <TableCell>
                            <div className="flex items-center gap-1">
                              <Coins className="h-4 w-4 text-amber-500" />
                              <span className="font-semibold">{user.points}</span>
                            </div>
                          </TableCell>
                          <TableCell className="text-right">
                            <div className="flex items-center justify-end gap-2">
                              <Button 
                                variant="outline" 
                                size="sm"
                                onClick={() => handleGrantPoints(user)}
                              >
                                <Plus className="h-4 w-4 mr-1" />
                                {t("admin.points.grant")}
                              </Button>
                              <Button 
                                variant="outline" 
                                size="sm"
                                onClick={() => handleDeductPoints(user)}
                              >
                                <Minus className="h-4 w-4 mr-1" />
                                {t("admin.points.deduct")}
                              </Button>
                            </div>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </div>
              )}

              {searchResults.length === 0 && searchQuery && !isSearching && (
                <div className="text-center py-8 text-muted-foreground">
                  {t("admin.points.noResults")}
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="history">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <History className="h-5 w-5" />
                {t("admin.points.recentTransactions")}
              </CardTitle>
            </CardHeader>
            <CardContent>
              {isLoading ? (
                <div className="text-center py-8">{t("common.loading")}</div>
              ) : transactions && transactions.length > 0 ? (
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>{t("admin.points.time")}</TableHead>
                      <TableHead>{t("admin.points.user")}</TableHead>
                      <TableHead>{t("admin.points.type")}</TableHead>
                      <TableHead>{t("admin.points.amount")}</TableHead>
                      <TableHead>{t("admin.points.balance")}</TableHead>
                      <TableHead>{t("admin.points.reason")}</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {transactions.map((tx: any) => (
                      <TableRow key={tx.id}>
                        <TableCell className="text-sm">
                          {format(new Date(tx.created_at), "yyyy-MM-dd HH:mm")}
                        </TableCell>
                        <TableCell>
                          <div className="font-medium">{tx.profiles?.display_name || tx.user_id}</div>
                        </TableCell>
                        <TableCell>
                          {getTransactionTypeBadge(tx.type, tx.amount)}
                        </TableCell>
                        <TableCell>
                          <span className={tx.amount > 0 ? "text-green-600" : "text-red-600"}>
                            {tx.amount > 0 ? "+" : ""}{tx.amount}
                          </span>
                        </TableCell>
                        <TableCell>{tx.balance_after}</TableCell>
                        <TableCell className="text-sm text-muted-foreground max-w-[200px] truncate">
                          {tx.reason || "-"}
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              ) : (
                <div className="text-center py-8 text-muted-foreground">
                  {t("admin.points.noTransactions")}
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      <GrantPointsDialog
        open={grantDialogOpen}
        onOpenChange={setGrantDialogOpen}
        user={selectedUser}
        type={grantType}
      />
    </div>
  );
};

export default AdminPoints;
